export default {
	data() {
		return {
			finish:false,
			cityName:'哈尔滨',
			cityList:[],
			visible:false,
			city:['国内','国外'],
			value:0,
			clubList:[]
		}
	},
	onLoad() {
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		this.cityName = uni.getStorageSync('city')
		this.getCity()
		this.getData()
	},
	methods: {
		goClub(id){
			uni.navigateTo({
				url:'../../pagesTwo/ClubIndex/ClubIndex?id='+id
			})
		},
		goJoin(id){
			uni.navigateTo({
				url:'../../pagesTwo/JoinClubContent/JoinClubContent?id='+id
			})
		},
		getData(){
			this.$api.join_club_list({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				city_name:this.cityName
			}).then(res=>{
				uni.hideLoading();
				if(res.data.code=='200'){
					this.clubList = res.data.data
					this.finish = true
				}
			})
		},
		ok(){
			this.cityName = this.cityList[this.value].name
			this.visible = false
			this.getData()
			this.value = 0
		},
		choose(){
			this.visible = true
			this.getCity()
		},
		cancel(){
			this.visible = false
			this.value = 0
		},
		fix(){
			this.visible = false
			this.value = 0
		},
		bindChange(e){
			if(e.detail.value.length==2){
				this.value = e.detail.value[1]
			}
			if(e.detail.value[0]==0){
				this.getCity()
				
			}else{
				this.getOther()
				
			}
		},
		getCity(){
			this.$api.city_choose({
			}).then(res=>{
				this.cityList = res.data.data.cn
			})
		},
		getOther(){
			this.$api.city_choose({
			}).then(res=>{
				this.cityList = res.data.data.other
			})
		}
	}
}
